<template>
  <div class="home" @mousemove="showCoords($event)">
    <div class="box" id="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import { reactive, onMounted } from 'vue'
export default {
  name: 'page',
  setup () {
    const data = reactive({})
    onMounted(() => {
      data.content = document.getElementById('content')
      data.WIDTN = document.body.clientWidth
      data.HEIGHT = document.body.clientHeight
      data.WIDTN_C = data.WIDTN / 2
      data.HEIGHT_C = data.HEIGHT / 2
    })
    const showCoords = (evt) => {
      data.content.style.setProperty('transform', `rotateY(${(evt.clientX - data.WIDTN_C) / 1000}deg) rotateX(${(data.HEIGHT_C - evt.clientY) / 1000}deg)`)
    }
    return { showCoords }
  }
}
</script>
<style lang="scss">
  .home{
    height: 100%;
    overflow-y:hidden;
    transform-style: preserve-3d;
    perspective: 100px;
    .box{
      height: calc(100% - 120px);
      background: rgba(255,255,255,0.2);
      width: 60%;
      margin: 60px auto;
      color: #ffffff;
      transform: rotateY(0deg) rotateX(0deg)
    }
  }
  .home::-webkit-scrollbar {
    /*滚动条区域的样式*/
    width: 1px;
    height: 1px;
  }
  .home::-webkit-scrollbar-thumb {
    /*滑动块样式*/
    width: 1px;
    height: 1px;
  }
  .home::-webkit-scrollbar-track {
    /*滑动轨道样式*/
    width: 1px;
    height: 1px;
  }
</style>
